header{
    position: fixed;
    width: 100%;
    display: flex;
    background: white;
    border-bottom: 2px solid #c2c2c2;
}
.logo {
    width: 32%;
    color: white;
    font-weight: 700;
    text-align: center;
}
.mynav {
    width: 36%;
    color: black;
    font-size: 20px;
    font-weight: 700;
    text-align: center;
}
ul {
    margin: 0;
}
ul > li {
    list-style: none;
    width: 100%;
}
input {
    width: 100%;
    height: 32px;
}
.menu {
    background: cadetblue;
    position: absolute;
    line-height: 1em;
    text-align: left;
    width: 65%;
    max-width: 400px;
}
.menu > a {
    color: white;
    border-bottom: 2px solid darkgreen;
    padding: 10px;
    display: block;
}
.cart {
    width: 32%;
    color: black;
    font-weight: 700;
    text-align: center;
}

.content {
    display: flex;
    flex-wrap: wrap;
    margin: 50px auto;
    max-width: 1024px;
}
.Card {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    text-align: center;
    width: 45%;
    padding: 15px;
    min-width: 350px;
    margin: 0 auto;
}
.Card > img {

}
.singleCard {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    text-align: center;
    width: auto;
    padding: 15px;
    margin: 0 auto;
}
.Container {
    padding: 0;
    margin: 0;
    width: 100%;
}
img {
    width: 100%;
    max-width: 600px;
}
.price {
    color: darkred;
    font-weight: bolder;
}
a {
    color: black;
    text-decoration: none;
}
button {
    background: cadetblue;
    font-weight: bolder;
    font-size: large;
    width: 100%;
    line-height: 2em;
    color: white;
    border: 1px solid darkseagreen;

}
.back-button{
    width: auto;
    display: block;
    background: transparent;
    border: none;
    color: black;
}
.loading {
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: xx-large;
}

.spinner {
    width: 70px;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: xx-large;
}

.spinner > div {
    width: 18px;
    height: 18px;
    background-color: cadetblue;

    border-radius: 100%;
    display: inline-block;
    -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
    animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.spinner .bounce1 {
    -webkit-animation-delay: -0.32s;
    animation-delay: -0.32s;
}

.spinner .bounce2 {
    -webkit-animation-delay: -0.16s;
    animation-delay: -0.16s;
}

.place-order{
    background: #f3f3f3;
    width: 85%;
    max-width: 500px;
    margin: 10% auto;
    display: grid;
    padding: 5%;
    border-radius: 1rem;
    box-shadow: 0px 0px 4px darkgrey;
}
.place-order > input {
    margin-bottom: 1em;
    padding: 5px 10px;
    width: auto;
}
.page-title{
    width: 100%;
    text-align: center;
    color: #3c3c3c;
    font-size: 2rem;
}
.item-title{
    font-size: 1.1rem;
    color: #3c3c3c;
}

footer{
    position: fixed;
    bottom: 0px;
    width: 100%;
    display: flex;
    background: cadetblue;
    border-top: 2px solid #c2c2c2;
}

@-webkit-keyframes sk-bouncedelay {
    0%, 80%, 100% { -webkit-transform: scale(0) }
    40% { -webkit-transform: scale(1.0) }
}

@keyframes sk-bouncedelay {
    0%, 80%, 100% {
        -webkit-transform: scale(0);
        transform: scale(0);
    } 40% {
          -webkit-transform: scale(1.0);
          transform: scale(1.0);
      }
}